//This a special component just used for marking attendance.
'use client'
import '../../public/css/bootstrap.css';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { FormGroup } from 'react-bootstrap';

function Attendance(p) {

  const{employee,dispatch} = p;

  const options = [5,4,3,2,1]

  let template = 
  `Notes From Class:`

  return (
    <Form>
      <h1>{employee.Name}</h1>
      
      <Form.Group as={Row} className="">

        <Col sm={5} className='mt-1'>


            <div key={`inline-radio`} className="mb-3">
              <Form.Check
                className='text-dark-emphasis'
                onChange={e=>dispatch({type:'update-Radial',value:1, label:'attendance', ticket:employee.ID})}
                inline
                checked={employee.Attendance == 1 ? true : false}
                label="Attended"
                name="group1"
                type='radio'
                id={`inline-radio-1`}
              />
              <Form.Check
                className='text-dark-emphasis'
                inline
                onChange={e=>dispatch({type:'update-Radial',value:2, label:'attendance', ticket:employee.ID})}
                checked={employee.Attendance == 2 ? true : false}
                label="Did Not Attend"
                name="group1"
                type='radio'
                id={`inline-radio-2`}
              />
              <Form.Check
                className='text-dark-emphasis'
                inline
                onChange={e=>dispatch({type:'update-Radial',value:3, label:'attendance', ticket:employee.ID})}
                checked={employee.Attendance == 3 ? true : false}
                label="Requires Retake"
                type='radio'
                id={`inline-radio-3`}
              />
            </div>


        </Col>
        
        <Col className='mt-1' sm={1}>        
          <Form.Label className='text-dark-emphasis badge'>
            Engagement
          </Form.Label>
        </Col>

        <Col className='ms-1' sm={1}>
        <Form.Select 
            onChange={e=>dispatch({type:'update-Values',value:e.target.value, label:'Engagement', ticket:employee.ID})}
            value={employee.Engagement}>  
            {options.map(o=>{return <option key={o} value={o}>{o}</option> })}
          </Form.Select>
        </Col>

        <Col className='mt-1' sm={1}>        
          <Form.Label className='text-dark-emphasis badge'>
            Comprehension
          </Form.Label>
        </Col>

        <Col className='ms-4' sm={1}>
          <Form.Select 
            onChange={e=>dispatch({type:'update-Values',value:e.target.value, label:'Comprehension', ticket:employee.ID})}
            value={employee.Comprehension}>
            {options.map(o=>{return <option key={o} value={o}>{o}</option> })}
          </Form.Select>
        </Col>
      </Form.Group>

      <FormGroup as={Row}>
        <Col sm={10}>
          <Form.Label>Class Notes</Form.Label>
          <Form.Control as="textarea" rows={3} value={employee.Notes} onChange={e=>dispatch({type:'update-Values',value:e.target.value, label:'Notes', ticket:employee.ID})} />
        </Col>
      </FormGroup>

    </Form>
  );
}

export default Attendance;