'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";
import Attendance from "@/app/_components/attendance";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import SelectEntry from "@/app/_components/selectentry";
import FormDescription from "@/app/_components/formdescription";

let pageDescription = ['Take attendance and provide feedback on each learner in a specific LCS Classroom session.']

let submitObject = {};

const initState = {
    'LCS Account Number': {
        value: ''
    },
    'Total Purchased':{
        value:1
    },
    'Submit':{
        Color:'primary disabled',
        Text:'Submit'
    },
    'Select Session':{
        options: [],
        value:0
    },
    'Learners':[
    ],
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    },
    'IsTTT': false
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Values':{

            //Yes this is confusing for this one page. 

            //This handels the attendance stuff

            let Learner = state['Learners'].find(l=>l.ID==action.ticket);

            Learner[action.label] = action.value;
            
            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit'

            return newState

            break
        }
        case 'update-Value':{

            console.dir(action);

            newState[action.label].value = action.value;

            return newState

            break
        }
        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }
        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }
        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }
        case 'load-SelectBox':{

            let Sessions = action.data.Sessions;

            newState['Select Session'].options = Sessions;

            newState['Select Session'].value = Sessions[0].ID;

            return newState

            break

        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }
        case 'load-Response':{

            newState['Learners'] = action.data.data.Tickets;
            newState['Submit'].Color = 'primary';

            return newState
            
            break
        }
        case 'update-Radial':{

            let Learner = state['Learners'].find(l=>l.ID==action.ticket);

            Learner.Attendance = action.value;

            return newState
        }

    }
};

export default function AddPurchase(){


    const [state, dispatch] = useReducer(reducer,initState);

    useEffect(function(){

                const loadDropListOptions = async()=>{

                    let options = await fetch({method:'GET',endpoint:'Litmos/RetrieveUpcomingSessions'});

                    if(options.didError){

                        //Do stuff

                        return

                    }

                    dispatch({type:'load-SelectBox',data:options.data})

                }

                loadDropListOptions();
            
                return
            },[]
    );



    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>


            <Container>


                <Row><FormTitle text='Mark Attendance'></FormTitle></Row>
                <FormDescription text={pageDescription}></FormDescription>
                <Col xs={8}>
                    <SelectEntry
                        label= 'Select Session'
                        value = {state['Select Session'].value}
                        dispatch = {dispatch}
                        endpoint = 'Litmos/LoadLearnersTickets'
                        data = {{SessionID:state['Select Session'].value}}
                        options={state['Select Session'].options}>
                    </SelectEntry>
                </Col>
                
                {state.Learners.map(l=>(

                    <Row className="mt-2">

                        <Attendance employee={l} dispatch={dispatch}></Attendance>

                    </Row>
                ))}

                <Row>
                    <Col className="mt-2">
                    <Submit
                method="POST"
                endpoint="Litmos/MarkAttendance"
                dispatch={dispatch}
                data={{Learners:state.Learners, MasterTicket: state['Select Session'].value, IsTTT: state.IsTTT}}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
                    </Col>

            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>

                </Row>

            </Container>

                                 

            
        </main>
    );
}